import { Component, OnInit, ViewChild } from '@angular/core';
import { MasterService } from '../master.service';
import { NotifierService } from 'angular-notifier';
import { ActivityMaster } from './activity-master';
import { MatPaginator, MatSort, MatTableDataSource } from '@angular/material';
import { NgForm } from '@angular/forms';
import { FormBuilder, Validators } from '@angular/forms';
import { ValidationService } from '../../validation/validation-service';

@Component({
  selector: 'app-activity-master',
  templateUrl: './activity-master.component.html',
  styleUrls: ['./activity-master.component.scss']
})
export class ActivityMasterComponent implements OnInit {
  activityForm: any;
  public activity: ActivityMaster;
  public activitys: ActivityMaster[];
  private notifier: NotifierService;
  constructor(notifierService: NotifierService, public masterService: MasterService, private formBuilder: FormBuilder) {
    this.notifier = notifierService;
    this.activityForm = this.formBuilder.group({
      'activityType': ['', [Validators.required, ValidationService.nameValidation]]
    });
  }
  displayedColumns: String[] = ['activityType', 'actions'];
  dataSource;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  ngOnInit() {
    this.activity = new ActivityMaster();
    this.getAllActivity();
  }

  applyFilter(filterValue: String) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  /** get all activity master details */
  getAllActivity() {
    this.masterService.getAllActivityMaster().subscribe((data) => {
      this.activitys = data;
      // this.dataSource = new MatTableDataSource(data);
      // this.dataSource.paginator = this.paginator;
      // this.dataSource.sort = this.sort;
    }, (err) => {
      console.log(err);
    });
  }

  /** save&update the activity master  */
  saveActivityMaster(exampleForm: NgForm) {
    if (this.activity._id === undefined || this.activity._id === '') {
      this.activity.createdBy = localStorage.getItem('user');
      this.masterService.saveActivityMaster(this.activity).subscribe((data) => {
        this.notifier.notify('success', 'Saved Successfully');
        exampleForm.resetForm();
        this.getAllActivity();
      }, (err) => {
        this.notifier.notify('error', 'Not Save');
      });
    } else {
      this.activity.modifiedBy = localStorage.getItem('user');
      this.masterService.updateActivityMaster(this.activity).subscribe((data) => {
        this.notifier.notify('success', 'Updated Successfully');
        exampleForm.resetForm();
        this.getAllActivity();
        this.activity._id = '';
      }, (err) => {
        this.notifier.notify('error', 'Not Update');
      });
    }
  }

  /** get details for updation */
  updateActivityMaster(i: Number, _id: String, activityType: String) {
    this.activity._id = _id;
    this.activity.activityType = activityType;
  }

  /**delete Activity Master */
  deleteActivityMaster(i: Number, _id: String, activityType: String) {
    this.activity._id = _id;
    this.masterService.deleteActivityMaster(this.activity).subscribe((data) => {
      this.notifier.notify('success', 'Deleted Successfully');
      this.getAllActivity();
      this.activity._id = '';
    }, (err) => {
      this.notifier.notify('error', 'Nor delate');
    });
  }

  refresh() {
    this.getAllActivity();
  }

  cancel(exampleForm: NgForm) {
    exampleForm.resetForm();
  }

}
