import { Component, OnInit, ViewChild } from '@angular/core';
import { MasterService } from '../master.service';
import { NotifierService } from 'angular-notifier';
import { MatTableDataSource, MatPaginator, MatSort } from '@angular/material';
import { NgForm } from '@angular/forms';
import { VendorTypeMaster } from './vendor-type-master';
import { FormBuilder, Validators } from '@angular/forms';
import { ValidationService } from '../../validation/validation-service';

@Component({
  selector: 'app-vendor-type-master',
  templateUrl: './vendor-type-master.component.html',
  styleUrls: ['./vendor-type-master.component.scss']
})
export class VendorTypeMasterComponent implements OnInit {
  vendorForm: any;
  public vendorTypeMasters: VendorTypeMaster[];
  public vendorTypeMaster: VendorTypeMaster;
  private notifier: NotifierService;
  dataSource;
  displayedColumns = ['vendorType', 'actions'];

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(public masterService: MasterService, notifierService: NotifierService, private formBuilder: FormBuilder) {
    this.notifier = notifierService;
    this.vendorForm = this.formBuilder.group({
      'vendorType': ['', [Validators.required, ValidationService.nameValidation]]
    });
  }

  ngOnInit() {
    this.vendorTypeMaster = new VendorTypeMaster();
    this.getVendorTypeMaster();
  }

  applyFilter(filterValue: String) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  getVendorTypeMaster() {
    this.masterService.getVendorTypeMaster().subscribe((data) => {
      this.vendorTypeMasters = data;
      // this.dataSource = new MatTableDataSource(this.vendorTypeMasters);
      // this.dataSource.sort = this.sort;
      // this.dataSource.paginator= this.paginator;
    }, error => {
      console.log(error);
    });
  }

  /* Save Item Type Master */
  saveVendorTypeMaster(exampleForm: NgForm) {
    if (this.vendorTypeMaster._id === undefined || this.vendorTypeMaster._id === '') {
      this.vendorTypeMaster.createdBy = localStorage.getItem('user');
      this.masterService.saveVendorTypeMaster(this.vendorTypeMaster).subscribe((data) => {
        this.notifier.notify('success', 'Saved Successfully');
        this.getVendorTypeMaster();
        exampleForm.resetForm();
      }, (err) => {
        this.notifier.notify('error', 'Not Save');
      });
    } else {
      this.vendorTypeMaster.modifiedBy = localStorage.getItem('user');
      this.masterService.updateVendorTypeMaster(this.vendorTypeMaster).subscribe((data) => {
        this.notifier.notify('success', 'Updated Successfully');
        this.getVendorTypeMaster();
        exampleForm.resetForm();
        this.vendorTypeMaster._id = '';
      }, (err) => {
        this.notifier.notify('error', 'Not Update');
      });
    }

  }

  /**edit the Vendor Type master details */
  editVendorTypeMaster(i: number, _id: String, vendorType: String) {
    this.vendorTypeMaster._id = _id;
    this.vendorTypeMaster.vendorType = vendorType;
  }

  deleteVendorTypeMaster(i: number, _id: String) {
    this.vendorTypeMaster.modifiedBy = localStorage.getItem('user');
    this.vendorTypeMaster._id = _id;
    this.masterService.deleteVendorTypeMaster(this.vendorTypeMaster).subscribe((data) => {
      this.notifier.notify('success', 'Deleted Successfully');
      this.getVendorTypeMaster();
      this.vendorTypeMaster._id = '';
    }, error => {
      console.log(error);
    });

  }

  refresh() {
    this.getVendorTypeMaster();
  }

  cancel(exampleForm: NgForm) {
    exampleForm.resetForm();
  }

}
