import { Component, OnInit } from '@angular/core';
import { MasterService } from '../master.service';
import { NotifierService } from 'angular-notifier';
import { MatTableDataSource, MatPaginator, MatSort } from '@angular/material';
import { NgForm } from '@angular/forms';
import { FormBuilder, Validators } from '@angular/forms';
import { ValidationService } from '../../validation/validation-service';
import { ItemCategoryMaster } from './item-category-master';

@Component({
  selector: 'app-item-category-master',
  templateUrl: './item-category-master.component.html',
  styleUrls: ['./item-category-master.component.scss']
})
export class ItemCategoryMasterComponent implements OnInit {

  roleForm: any;
  public itemCategoryList: ItemCategoryMaster[];
  public itemCategoryMaster: ItemCategoryMaster;
  private notifier: NotifierService;
  dataSource;
  displayedColumns = ['itemCategory', 'actions'];

  constructor(public masterService: MasterService, notifierService: NotifierService, private formBuilder: FormBuilder) {
    this.notifier = notifierService;
    this.roleForm = this.formBuilder.group({
      'itemCategory': ['', [Validators.required, ValidationService.nameValidation]]
    });
  }

  ngOnInit() {
    this.itemCategoryMaster=new ItemCategoryMaster();
    this.getItemCategoryMaster();
  }

  getItemCategoryMaster() {
    this.masterService.getItemCategoryMaster().subscribe((data) => {
      this.itemCategoryList = data;
      // console.log('data',data);
      // this.dataSource = new MatTableDataSource(this.roleMasters);
      // this.dataSource.sort = this.sort;
      // this.dataSource.paginator= this.paginator;
    }, error => {
      console.log(error);
    });
  }

  /* Save Role Master */
  saveItemCategory(exampleForm: NgForm) {
    if (this.itemCategoryMaster._id === undefined || this.itemCategoryMaster._id === '') {
      this.itemCategoryMaster.createdBy = localStorage.getItem('user');
      this.masterService.saveItemCategoryMaster(this.itemCategoryMaster).subscribe((data) => {
        this.notifier.notify('success', 'Saved Successfully');
        this.getItemCategoryMaster();
        exampleForm.resetForm();
      }, (err) => {
        this.notifier.notify('error', 'Not Save');
      });
    } else {
      this.itemCategoryMaster.modifiedBy = localStorage.getItem('user');
      this.masterService.updateItemCategoryMaster(this.itemCategoryMaster).subscribe((data) => {
        this.notifier.notify('success', 'Updated Successfully');
        this.getItemCategoryMaster();
        exampleForm.resetForm();
        this.itemCategoryMaster._id = '';
      }, (err) => {
        this.notifier.notify('error', 'Not Update');
      });
    }

  }

  /**edit the Role master details */
  editItemCategory(i: number, _id: String, itemCategory: String) {
    this.itemCategoryMaster._id = _id;
    this.itemCategoryMaster.itemCategory = itemCategory;
  }

  deleteItemCategory(i: number, _id: String) {
    this.itemCategoryMaster.modifiedBy = localStorage.getItem('user');
    this.itemCategoryMaster._id = _id;
    this.masterService.deleteItemCategoryMaster(this.itemCategoryMaster).subscribe((data) => {
      this.notifier.notify('success', 'Deleted Successfully');
      this.getItemCategoryMaster();
      this.itemCategoryMaster._id = '';
    }, error => {
      console.log(error);
    });

  }

  refresh() {
    this.getItemCategoryMaster();
  }

  cancel(exampleForm: NgForm) {
    exampleForm.resetForm();
  }
}
