import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MasterComponent } from './master.component';
import { MasterRoutingModule } from './master-routing.module';
import { MaterialModule } from 'src/app/app.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { NotifierModule, NotifierOptions } from 'angular-notifier';
import { VendorTypeMasterComponent } from './vendor-type-master/vendor-type-master.component';
import { ItemTypeMasterComponent } from './item-type-master/item-type-master.component';
import { MoldingMasterComponent } from './molding-master/molding-master.component';
import { RoleMasterComponent } from './role-master/role-master.component';
import { ActivityMasterComponent } from './activity-master/activity-master.component';
import { UnitMasterComponent } from './unit-master/unit-master.component';
import { FoundryModule } from '../foundry.module';
import {FlexLayoutModule} from '@angular/flex-layout';
import { PerfectScrollbarModule, PerfectScrollbarConfigInterface,
  PERFECT_SCROLLBAR_CONFIG } from 'ngx-perfect-scrollbar';
import { ItemCategoryMasterComponent } from './item-category-master/item-category-master.component';
const customNotifierOptions: NotifierOptions = {
  position: {
    horizontal: {
      position: 'middle',
      distance: 12
    },
    vertical: {
      position: 'top',
      distance: 50,
      gap: 10
    }
  }, 
  theme: 'material',
  behaviour: {
    autoHide: 1000,
    onClick: 'hide',
    onMouseover: 'pauseAutoHide',
    showDismissButton: true,
    stacking: 4
  },
  animations: {
    enabled: true,
    show: {
      preset: 'slide',
      speed: 300,
      easing: 'ease'
    },
    hide: {
      preset: 'fade',
      speed: 300,
      easing: 'ease',
      offset: 50
    },
    shift: {
      speed: 300,
      easing: 'ease'
    },
    overlap: 10
  }
};

const DEFAULT_PERFECT_SCROLLBAR_CONFIG: PerfectScrollbarConfigInterface = {
  wheelPropagation: true
};

@NgModule({
  declarations: [MasterComponent, VendorTypeMasterComponent, ItemTypeMasterComponent, MoldingMasterComponent,
    RoleMasterComponent, ActivityMasterComponent, UnitMasterComponent, ItemCategoryMasterComponent],
  imports: [
    CommonModule, MasterRoutingModule, MaterialModule, FormsModule, ReactiveFormsModule,
    NotifierModule.withConfig(customNotifierOptions), FoundryModule, PerfectScrollbarModule,
    FlexLayoutModule,
  ],
  providers: [
    {
      provide: PERFECT_SCROLLBAR_CONFIG,
      useValue: DEFAULT_PERFECT_SCROLLBAR_CONFIG
    }
  ]
})
export class MasterModule { }
