import { Component, OnInit, ViewChild } from '@angular/core'; import { MasterService } from '../master.service';
import { NotifierService } from 'angular-notifier';
import { MatTableDataSource, MatPaginator, MatSort } from '@angular/material';
import { NgForm } from '@angular/forms';
import { MoldingMaster } from './molding-master';
import { FormBuilder, Validators } from '@angular/forms';
import { ValidationService } from '../../validation/validation-service';

@Component({
  selector: 'app-molding-master',
  templateUrl: './molding-master.component.html',
  styleUrls: ['./molding-master.component.scss']
})
export class MoldingMasterComponent implements OnInit {
  moldingForm: any;
  public moldingMasters: MoldingMaster[];
  public moldingMaster: MoldingMaster;
  private notifier: NotifierService;
  dataSource;
  displayedColumns = ['moldType', 'actions'];

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(public masterService: MasterService, notifierService: NotifierService, private formBuilder: FormBuilder) {
    this.notifier = notifierService;
    this.moldingForm = this.formBuilder.group({
      'moldType': ['', [Validators.required, ValidationService.nameValidation]]
    });
  }

  ngOnInit() {
    this.moldingMaster = new MoldingMaster();
    this.getMoldingMaster();
  }

  applyFilter(filterValue: String) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  getMoldingMaster() {
    this.masterService.getMoldingMaster().subscribe((data) => {
      this.moldingMasters = data;
      // this.dataSource = new MatTableDataSource(this.moldingMasters);
      // this.dataSource.sort = this.sort;
      // this.dataSource.paginator = this.paginator;
    }, error => {
      console.log(error);
    });
  }

  /* Save Molding Master */
  saveMoldingMaster(exampleForm: NgForm) {
    if (this.moldingMaster._id === undefined || this.moldingMaster._id === '') {
      this.moldingMaster.createdBy = localStorage.getItem('user');
      this.masterService.saveMoldingMaster(this.moldingMaster).subscribe((data) => {
        this.notifier.notify('success', 'Saved Successfully');
        this.getMoldingMaster();
        exampleForm.resetForm();
      }, (err) => {
        this.notifier.notify('error', 'Not Save');
      });
    } else {
      this.moldingMaster.modifiedBy = localStorage.getItem('user');
      this.masterService.updateMoldingMaster(this.moldingMaster).subscribe((data) => {
        this.notifier.notify('success', 'Updated Successfully');
        this.getMoldingMaster();
        exampleForm.resetForm();
        this.moldingMaster._id = '';
      }, (err) => {
        this.notifier.notify('error', 'Not Update');
      });
    }

  }

  /**edit the Molding master details */
  editMoldingMaster(i: number, _id: String, moldType: String) {
    this.moldingMaster._id = _id;
    this.moldingMaster.moldType = moldType;
  }

  deleteMoldingMaster(i: number, _id: String) {
    this.moldingMaster.modifiedBy = localStorage.getItem('user');
    this.moldingMaster._id = _id;
    this.masterService.deleteMoldingMaster(this.moldingMaster).subscribe((data) => {
      this.notifier.notify('success', 'Deleted Successfully');
      this.getMoldingMaster();
      this.moldingMaster._id = '';
    }, error => {
      console.log(error);
    });

  }

  refresh() {
    this.getMoldingMaster();
  }

  cancel(exampleForm: NgForm) {
    exampleForm.resetForm();
  }
}
