import { Component, OnInit, ViewChild } from '@angular/core';
import { MasterService } from '../master.service';
import { NotifierService } from 'angular-notifier';
import { MatTableDataSource, MatPaginator, MatSort } from '@angular/material';
import { NgForm } from '@angular/forms';
import { RoleMaster } from './role-master';
import { FormBuilder, Validators } from '@angular/forms';
import { ValidationService } from '../../validation/validation-service';

@Component({
  selector: 'app-role-master',
  templateUrl: './role-master.component.html',
  styleUrls: ['./role-master.component.scss']
})
export class RoleMasterComponent implements OnInit {
  roleForm: any;
  public roleMasters: RoleMaster[];
  public roleMaster: RoleMaster;
  private notifier: NotifierService;
  dataSource;
  displayedColumns = ['role', 'actions'];

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(public masterService: MasterService, notifierService: NotifierService, private formBuilder: FormBuilder) {
    this.notifier = notifierService;
    this.roleForm = this.formBuilder.group({
      'role': ['', [Validators.required, ValidationService.nameValidation]]
    });
  }

  ngOnInit() {
    this.roleMaster = new RoleMaster();
    this.getRoleMaster();
  }

  applyFilter(filterValue: String) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  getRoleMaster() {
    this.masterService.getRoleMaster().subscribe((data) => {
      this.roleMasters = data;
      // console.log('data',data);
      // this.dataSource = new MatTableDataSource(this.roleMasters);
      // this.dataSource.sort = this.sort;
      // this.dataSource.paginator= this.paginator;
    }, error => {
      console.log(error);
    });
  }

  /* Save Role Master */
  saveRoleMaster(exampleForm: NgForm) {
    if (this.roleMaster._id === undefined || this.roleMaster._id === '') {
      this.roleMaster.createdBy = localStorage.getItem('user');
      this.masterService.saveRoleMaster(this.roleMaster).subscribe((data) => {
        this.notifier.notify('success', 'Saved Successfully');
        this.getRoleMaster();
        exampleForm.resetForm();
      }, (err) => {
        this.notifier.notify('error', 'Not Save');
      });
    } else {
      this.roleMaster.modifiedBy = localStorage.getItem('user');
      this.masterService.updateRoleMaster(this.roleMaster).subscribe((data) => {
        this.notifier.notify('success', 'Updated Successfully');
        this.getRoleMaster();
        exampleForm.resetForm();
        this.roleMaster._id = '';
      }, (err) => {
        this.notifier.notify('error', 'Not Update');
      });
    }

  }

  /**edit the Role master details */
  editRoleMaster(i: number, _id: String, role: String) {
    this.roleMaster._id = _id;
    this.roleMaster.role = role;
  }

  deleteRoleMaster(i: number, _id: String) {
    this.roleMaster.modifiedBy = localStorage.getItem('user');
    this.roleMaster._id = _id;
    this.masterService.deleteRoleMaster(this.roleMaster).subscribe((data) => {
      this.notifier.notify('success', 'Deleted Successfully');
      this.getRoleMaster();
      this.roleMaster._id = '';
    }, error => {
      console.log(error);
    });

  }

  refresh() {
    this.getRoleMaster();
  }

  cancel(exampleForm: NgForm) {
    exampleForm.resetForm();
  }

}

