import { Component, OnInit, ViewChild } from '@angular/core';
import { UnitMaster } from './unit-master';
import { MasterService } from '../master.service';
import { NotifierService } from 'angular-notifier';
import { MatPaginator, MatSort, MatTableDataSource } from '@angular/material';
import { NgForm } from '@angular/forms';
import { FormBuilder, Validators } from '@angular/forms';
import { ValidationService } from '../../validation/validation-service';

@Component({
  selector: 'app-unit-master',
  templateUrl: './unit-master.component.html',
  styleUrls: ['./unit-master.component.scss']
})
export class UnitMasterComponent implements OnInit {
  unitForm: any;
  public unit: UnitMaster;
  public units: UnitMaster[];
  private notifier: NotifierService;
  constructor(public masterService: MasterService, notifierService: NotifierService, private formBuilder: FormBuilder) {
    this.notifier = notifierService;
    this.unitForm = this.formBuilder.group({
      'unitType': ['', [Validators.required, ValidationService.nameValidation]]
    });
  }

  displayedColumns: String[] = ['unitType', 'actions'];
  dataSource;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  ngOnInit() {
    this.unit = new UnitMaster();
    this.getAllUnitMaster();
  }

  applyFilter(filterValue: String) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }
  /** get all unit details */
  getAllUnitMaster() {
    this.masterService.getAllUnitMaster().subscribe((data) => {
      this.units = data;
      // this.dataSource = new MatTableDataSource(data);
      // this.dataSource.paginator = this.paginator;
      // this.dataSource.sort = this.sort;
    }, (err) => {
      console.log(err);
    });
  }

  /** save&update the unit master  */
  saveUnitMaster(exampleForm: NgForm) {
    if (this.unit._id === '' || this.unit._id === undefined) {
      this.unit.createdBy = localStorage.getItem('user');
      this.masterService.saveUnitMaster(this.unit).subscribe((data) => {
        this.notifier.notify('success', 'Saved Successfully');
        exampleForm.resetForm();
        this.getAllUnitMaster();
      }, (err) => {
        this.notifier.notify('error', 'Not Save');
      });
    } else {
      this.unit.modifiedBy = localStorage.getItem('user');
      this.masterService.updateUnitMaster(this.unit).subscribe((data) => {
        this.notifier.notify('success', 'Updated Successfully');
        exampleForm.resetForm();
        this.getAllUnitMaster();
        this.unit._id = '';
      }, (err) => {
        this.notifier.notify('error', 'Not Update');
      });
    }
  }

  /** get the details for updation  */
  editUnitTypeMaster(i: Number, _id: String, unitType: String) {
    this.unit._id = _id;
    this.unit.unitType = unitType;
  }

  /**delete Unit Master */
  deleteUnitMaster(i: Number, _id: String, unitType: String) {
    this.unit._id = _id;
    this.masterService.deleteUnitMaster(this.unit).subscribe((data) => {
      this.notifier.notify('success', 'Deleted Successfully');
      this.getAllUnitMaster();
      this.unit._id = '';
    }, (err) => {
      this.notifier.notify('error', 'Nor delate');
    });
  }


  refresh() {
    this.getAllUnitMaster();
  }

  cancel(exampleForm: NgForm) {
    exampleForm.resetForm();
  }
}
