import { Component, OnInit, ViewChild } from '@angular/core';
import { ItemTypeMaster } from '../item-type-master/item-type-master';
import { MasterService } from '../master.service';
import { NotifierService } from 'angular-notifier';
import { MatTableDataSource, MatPaginator, MatSort } from '@angular/material';
import { NgForm } from '@angular/forms';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ValidationService } from '../../validation/validation-service';
@Component({
  selector: 'app-item-type-master',
  templateUrl: './item-type-master.component.html',
  styleUrls: ['./item-type-master.component.scss']
})
export class ItemTypeMasterComponent implements OnInit {
  itemForm: any;
  public itemTypeMasters: ItemTypeMaster[];
  public itemTypeMaster: ItemTypeMaster;
  private notifier: NotifierService;
  dataSource;
  displayedColumns = ['itemType','stockType', 'actions'];


  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(public masterService: MasterService, notifierService: NotifierService, private formBuilder: FormBuilder) {
    this.notifier = notifierService;
    this.itemForm = this.formBuilder.group({
      'itemType': ['', [Validators.required, ValidationService.nameValidation]],
      'stockType':['', [Validators.required, ValidationService.nameValidation]]
    });
  }



  ngOnInit() {
    this.itemTypeMaster = new ItemTypeMaster();
    this.getItemTypeMaster();
  }

  applyFilter(filterValue: String) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  getItemTypeMaster() {
    this.masterService.getItemTypeMaster().subscribe((data) => {
      this.itemTypeMasters = data;
      console.log('data', data);
      // this.dataSource = new MatTableDataSource(this.itemTypeMasters);
      // this.dataSource.sort = this.sort;
      // this.dataSource.paginator= this.paginator;
    }, error => {
      console.log(error);
    });
  }

  /* Save Item Type Master */
  saveItemTypeMaster(exampleForm: NgForm) {
    if (this.itemTypeMaster._id === undefined || this.itemTypeMaster._id === '') {
      this.itemTypeMaster.createdBy = localStorage.getItem('user');
      this.masterService.saveItemTypeMaster(this.itemTypeMaster).subscribe((data) => {
        this.notifier.notify('success', 'Saved Successfully');
        this.getItemTypeMaster();
        exampleForm.resetForm();
      }, (err) => {
        this.notifier.notify('error', 'Not Save');
      });
    } else {
      this.itemTypeMaster.modifiedBy = localStorage.getItem('user');
      this.masterService.updateItemTypeMaster(this.itemTypeMaster).subscribe((data) => {
        this.notifier.notify('success', 'Updated Successfully');
        this.getItemTypeMaster();
        exampleForm.resetForm();
        this.itemTypeMaster._id = '';
      }, (err) => {
        this.notifier.notify('error', 'Not Update');
      });
    }

  }

  /**edit the Item Type master details */
  editItemTypeMaster(i: number, _id: String, itemType: String,stockType:String) {
    this.itemTypeMaster._id = _id;
    this.itemTypeMaster.itemType = itemType;
    this.itemTypeMaster.stockType=stockType;
  }

  deleteItemTypeMaster(i: number, _id: String) {
    this.itemTypeMaster.modifiedBy = localStorage.getItem('user');
    this.itemTypeMaster._id = _id;
    this.masterService.deleteItemTypeMaster(this.itemTypeMaster).subscribe((data) => {
      this.notifier.notify('success', 'Deleted Successfully');
      this.getItemTypeMaster();
      this.itemTypeMaster._id = '';
    }, error => {
      console.log(error);
    });

  }

  refresh() {
    this.getItemTypeMaster();
  }

  cancel(exampleForm: NgForm) {
    exampleForm.resetForm();
  }

}
